<template>
  <div
    class="blogComment-container"
    ref="blogCommentContainer"
    id="detailMessage"
  >
    <MessageArea
      :list="data.rows"
      title="评论列表"
      :subTitle="data.total"
      :isListLoading="isLoading"
      @hanldeSubmit="hanldeSubmit"
    />
  </div>
</template>

<script>
import fetchData from "@/mixins/fetchData.js";
import MessageArea from "@/components/MessageArea/";
import { getComment, postComment } from "@/api/blog.js";
export default {
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  mixins: [fetchData([])],
  components: { MessageArea },
  created() {
    this.$bus.$on("mainScroll", this.mainScroll);
  },
  computed: {
    isOver() {
      return this.data.rows.length < this.data.total;
    },
  },
  methods: {
    mainScroll(dom) {
      if (this.isLoading) {
        return;
      }
      const range = 100;
      const des = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight);
      if (des <= range) {
        this.fetchmMore();
      }
    },
    async fetchData() {
      const resp = await getComment(
        this.$route.params.id,
        this.page,
        this.limit
      );
      return resp.data;
    },
    async hanldeSubmit(data, callback) {
      const resp = await postComment(data);
      setTimeout(() => {
        callback("评论成功!");
        this.data.rows.unshift(resp.data);
        this.data.total++;
        return resp.data;
      }, 1000);
    },
    async fetchmMore() {
      if (!this.isOver) {
        return;
      }
      this.isLoading = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
  },
};
</script>

<style scoped lang="less">
.blogComment-container {
  position: relative;
}
</style>
