<template>
  <ul class="right-list-container">
    <li v-for="(item, i) in list" :key="i">
      <span @click="hanldeSelect(item)" :class="{ active: item.isSelect }">{{
        item.name
      }}</span>
      <span v-if="item.aside" :class="{ active: item.isSelect }">{{
        item.aside
      }}</span>
      <RightList :list="item.children" @select="hanldeSelect" />
    </li>
  </ul>
</template>

<script>
export default {
  name: "RightList",
  props: {
    list: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    hanldeSelect(item) {
      this.$emit("select", item);
    },
  },
};
</script>

<style scoped lang="less">
.right-list-container {
  list-style-type: none;
  padding: 0;
  .right-list-container {
    margin-left: 1em;
  }
  li {
    min-height: 40px;
    ul {
      li:nth-child(1) {
        margin-top: 10px;
      }
    }
    span {
      cursor: pointer;
      margin-right: 10px;
    }
    .active {
      color: red;
      font-weight: bold;
    }
  }
}
</style>
