<template>
  <div class="blog-toc-container">
    <h2>目录</h2>
    <RightList :list="totalSelect" @select="hanldeHash" />
  </div>
</template>

<script>
import { shake } from "@/utils/";
import RightList from "./rightList.vue";
export default {
  data() {
    return {
      anchor: "",
    };
  },
  props: {
    toc: {
      type: Array,
    },
  },
  components: {
    RightList,
  },
  created() {
    this.setSelectShake = shake(this.setSelect);
    this.$bus.$on("mainScroll", this.setSelectShake);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.setSelectShake);
  },
  methods: {
    hanldeHash(item) {
      location.hash = item.anchor;
    },
    setSelect() {
      this.anchor = "";
      const range = 200;
      for (const item of this.doms) {
        const top = item.getBoundingClientRect().top;
        if (!item) {
          continue;
        }
        if (top >= 0 && top <= range) {
          this.anchor = item.id;
          return;
        } else if (top >= range) {
          return;
        } else {
          this.anchor = item.id;
        }
      }
    },
  },
  computed: {
    totalSelect() {
      const getTOC = (toc = []) => {
        return toc.map((it) => ({
          ...it,
          isSelect: this.anchor == it.anchor,
          children: getTOC(it.children),
        }));
      };
      return getTOC(this.toc);
    },
    doms() {
      const result = [];
      const addTodoms = (toc) => {
        for (const item of toc) {
          const dom = document.getElementById(item.anchor);
          result.push(dom);
          if (item.children && item.children.length) {
            addTodoms(item.children);
          }
        }
      };
      addTodoms(this.toc);
      return result;
    },
  },
};
</script>

<style scoped lang="less">
.blog-toc-container {
  padding: 20px;
  width: 250px;
  height: 100%;
  box-sizing: border-box;

  h2 {
    margin: 0;
  }
}
</style>
