<template>
  <div class="detail-container">
    <Layout>
      <div class="test" ref="mainScroll" v-loading="isLoading">
        <BlogDetail :blog="data" v-if="data" />
        <BlogComment v-if="!isLoading" />
      </div>
      <template #right>
        <div class="right-container" v-loading="isLoading">
          <BlogTOC v-if="data" :toc="data.toc" />
        </div>
      </template>
    </Layout>
    <ToTop />
  </div>
</template>

<script>
import ToTop from "@/components/ToTop";
import BlogComment from "./components/BlogComment.vue";
import fetchData from "@/mixins/fetchData.js";
import { getBlogHtml } from "@/api/blog.js";
import Layout from "@/components/Layout";
import BlogTOC from "./components/BlogTOC.vue";
import BlogDetail from "./components/BlogDtail.vue";
import setMainScroll from "@/mixins/setMainScroll.js";
import { titleControl } from "@/utils";
export default {
  mixins: [fetchData(null), setMainScroll("mainScroll")],
  methods: {
    async fetchData() {
      const resp = await getBlogHtml(this.$route.params.id);
      titleControl.setTitle(resp.data.title);
      return resp.data;
    },
  },
  updated() {
    const hash = location.hash;
    location.hash = "";
    setTimeout(() => {
      location.hash = hash;
    }, 500);
  },
  components: {
    Layout,
    BlogDetail,
    BlogTOC,
    BlogComment,
    ToTop,
  },
};
</script>

<style scoped lang="less">
.detail-container {
  position: relative;
  height: 100%;
}
.test {
  position: relative;
  padding: 20px;
  height: 100%;
  box-sizing: border-box;
  overflow-y: auto;
  scroll-behavior: smooth;
}
.right-container {
  position: relative;
  height: 100%;
  width: 250px;
}
</style>
